import React, { useEffect, useState } from 'react';
import { Table, Button, Input, message } from 'antd';
import type { ColumnsType } from 'antd/es/table';
import { getTime } from './types/time';
import './App.css';

interface DataType {
  key?: React.Key;
  id: Number;
  name: string;
  time: number;
  done: boolean;
}


const App: React.FC = () => {

  const columns: ColumnsType<DataType> = [
    {
      title: '',
      dataIndex: 'name',
      render: (_, todo) => {
        return (
          <div className={todo.done ? 'todo-done' : 'done'}>{todo.name}</div>
        )
      }
    },
    {
      title: '',
      dataIndex: 'age',
      render: (_, todo) => {
        return (
          <span>{getTime(todo.time)}</span>
        )
      }
    },
    {
      title: '',
      dataIndex: 'address',
      render: (_, todo) => {
        return (
          <>
            <Button onClick={() => switchDone(todo.id)} type='primary'></Button>
            <Button onClick={() => deleteTodo(todo.id)} danger></Button>
          </>
        )
      }
    }
  ];

  const [todoList, setTodoList] = useState<Array<DataType>>([]);
  const [inpValue, setInpValue] = useState('')

  const addTodo = () => {
    const setTime = new Date().getTime()
    if (inpValue === '') {
      message.warn('')
      return
    }
    let todo = {
      key: setTime,
      id: setTime,
      name: inpValue,
      time: setTime,
      done: false,
    }
    setTodoList([...todoList, todo])
    saveTodo([...todoList, todo])
    message.success('')
  }

  const deleteTodo = (id: Number) => {
    const todo = [...todoList]
    const newTodo = todo.filter(todo => todo.id !== id)
    setTodoList([...newTodo])
    saveTodo([...newTodo])
    message.error('')
  }

  const switchDone = (id: Number) => {
    todoList.forEach(todo => {
      if (todo.id === id) {
        if (todo.done === false) {
          todo.done = true
          message.warn('')
        } else {
          todo.done = false
        }
        return
      }
    })
    setTodoList([...todoList])
    saveTodo([...todoList])
  }

  const saveTodo = (arr: Array<DataType>) => {
    localStorage.todoList = JSON.stringify(arr)
  }

  const loadTodo = () => {
    //  todoList 
    const todoList = localStorage.getItem('todoList')
    if (todoList) {
      setTodoList(JSON.parse(todoList))
    }
  }

  useEffect(() => {
    loadTodo()
  }, [])

  return (
    <>
      <Input placeholder="..." value={inpValue} onChange={(e) => setInpValue(e.target.value)} />
      <Button type='primary' onClick={() => addTodo()}></Button>
      <Table columns={columns} dataSource={todoList} />
    </>
  );
}

export default App;
